<template>
  <div class="about">
    <el-container>
      <el-aside width="200px">
        <el-menu  :default-active="activeindex?activeindex:1" router="true" class="el-menu-vertical-demo" @select="handleOpen" >
         <div v-for="(item,idx) in items" :key="idx">
         <el-menu-item :index="idx+1">
            <span slot="title">{{item}}</span>
          </el-menu-item>
        </div>
          
        </el-menu>
      </el-aside>
      <el-main>
        <router-view name="gs" ></router-view>
      </el-main>
    </el-container>
          

  </div>
</template>

<script>
import {mapState} from 'vuex'
  export default {
    el: "about",
    data() {
      return {
        items: ['公司简介', '服务架构', '公司文化', '公司的服务']
      }
      
    },
    computed:{
        ...mapState(['activeindex'])

    },
    mounted(){
     
     //只在程序加载的时候第一次切换 防止页面一直默认为选中1
        if(this.$store.state.activeindex=='0')
        {
        this.$store.commit('change_activeindex',"1")

        }
    },
    methods:{
      handleOpen(key) {
        this.$router.push({path:'/4/'+key})

        this.$store.commit('change_activeindex',key)
      }


    }
   
    
   
  }
</script>

<style>
  .el-aside {
    color: #333;
    text-align: center;
    line-height: 200px;
  }
  .el-main {
    color: #333;
    text-align: center;
    line-height: 160px;
  }
  body>.el-container {
    margin-bottom: 40px;
  }
</style>