import Vue from 'vue'
import Vuex from 'vuex'
Vue.use(Vuex)

 const store = new Vuex.Store({
     state:{
        activeindex:"0",
         msg:"你好啊",
         i:0,
         twomsg:"今天天气真的很好"
     },
     mutations: {
         change_activeindex(store,index){
             store.activeindex=index

         }
     },
     getters:{
         allstr(store){
             return store.msg+store.msg
         }

     },
     modules:{
        //  one:onestore,
        //  two:twostore
     }

    // ...
})
export default store